<template>
    <div class="page">
        <!--<h1></h1>-->
        <Nav :activeIndex="3"></Nav>


        <div class="page-content">
            <el-row>
                <template v-for="item in tpl">
                    <div class="tplItem-wrap">
                        <div class="tplItem">
                            <img :src="item.cover" alt="" class="tplItem-img">
                            <div class="tplItem-name">
                                {{''+item.name}}
                                <el-button type="text" size="mini" class="fr" @click="handleDelClick(item)">
                                </el-button>
                            </div>
                            <div class="tplItem-handle">
                            </div>
                        </div>
                    </div>
                </template>
            </el-row>
        </div>


    </div>
</template>
<script>
    import Nav from '@/components/Nav.vue'
    import {
        request,
        api
    } from '@/util/api'
    import util from '@/util/util'
    import dataHelper from '@/util/dataHelper'

    export default {
        name: 'HelloWorld',
        components: {
            Nav
        },
        data() {
            return {
                tpl: [
                    // 1, 2, 3, 4, 5, 6
                ]
            }
        },
        mounted() {
            this.getList();
        },
        methods: {
            getList() {
                request.get(api.tplList, {}).then((response) => {
                    console.log(response);
                    if (response.code === 200) {
                        this.tpl = dataHelper.tplList(response.data);
                        console.log(this.tpl);
                    } else {
                        this.$message({
                            message: response.msg,
                            showClose: true,
                            type: 'error'
                        });
                    }
                }).catch(function (error) {
                    console.log(error);
                });
            },
            handleDelClick(item) {

                console.log(item);

                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {

                    request.get(api.tplDel, {
                        params: {
                            model_id: item.id
                        }
                    }).then((response) => {
                        console.log(response);
                        if (response.code === 200) {
                            this.$message({
                                showClose: true,
                                type: 'success',
                                message: '!'
                            });
                            // 
                            this.getList();
                        } else {
                            this.$message({
                                message: response.msg,
                                showClose: true,
                                type: 'error'
                            });
                        }
                    }).catch(function (error) {
                        console.log(error);
                    });

                }).catch(() => {
                    this.$message({
                        showClose: true,
                        duration: 1000,
                        type: 'info',
                        message: ''
                    });
                });


            }
        }
    }
</script>

