module.exports = {
    // 30
    age: 30 * 24 * 60 * 60 * 1000,
    /**
     * 
     * @param age
     * @returns {exports}
     */
    setAge: function (age) {
        this.age = age;
        return this;
    },
    /**
     *  localStorage
     * @param key
     * @param value
     */
    set: function (key, value) {
        localStorage.removeItem(key);
        var isObject = value instanceof Object,
            _time = new Date().getTime(),
            _age = this.age;

        //  value 
        if (!isObject) {
            var b = value;
            value = {};
            value._value = b;
        }
        // 
        value._time = _time;
        // 
        value._age = _time + _age;
        // 
        value._isObject = isObject;
        localStorage.setItem(key, JSON.stringify(value));
        return this;
    },
    /**
     *  localStorage 
     * @param key
     * @returns {boolean}
     */
    isExpire: function (key) {

        var isExpire = true,
            value = localStorage.getItem(key),
            now = new Date().getTime();

        if (value) {
            value = JSON.parse(value);
            // 
            isExpire = now > value._age;
        } else {
            // 
        }
        return isExpire;
    },
    /**
     *  localStorage 
     * @param key
     * @returns {*}
     */
    get: function (key) {
        var isExpire = this.isExpire(key),
            value = null;
        if (!isExpire) {
            value = localStorage.getItem(key);
            value = JSON.parse(value);
            if (!value._isObject) {
                value = value._value;
            }
        }
        return value;
    }
};
